.group-about {
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 108px 40px 108px;
    gap: 30px;


    @media (min-width: 768px) and (max-width: 1024px) {
        margin: 0 20px 20px 20px;
    }

    @media screen and (min-width: 320px) and (max-width: 767.98px) {
        margin: 0 0 20px 0;
        gap: 20px;
    }
}