$container-color: #FF6060;
$title-color: white;

.group-collapse {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 52px;
    border-radius: 5px;
    padding: 10px 20px 10px 15px;
    background-color: $container-color;
    cursor: pointer;

    span {
        color: $title-color;
        font-weight: 700;
        font-size: 24px;
    }
}

.chevron {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.3s ease;

    &.open {
        transform: rotate(-180deg);
    }
}

.text {
    padding: 0 15px;
    border-radius: 10px 10px 5px 5px;
    background-color: rgba(246, 246, 246, 0.45);
    font-weight: 400;
    font-size: 18px;
    max-height: 0;
    opacity: 0;
    transition: max-height 0.3s ease, opacity 0.3s ease, padding 0.3s ease;

    &.open {
        padding: 15px;
        max-height: 1000px;
        visibility: visible;
        height: auto;
        opacity: 1;
     }

    li {
        list-style-type: none;
    }
}

@media screen and (min-width: 768px) and (max-width: 1056px) {
    .text{
        font-size: 15px;
    }
}

@media screen and (min-width: 320px) and (max-width: 767.98px) {
    .group-collapse {
        height: 30px;
        padding: 10px;

        span {
            font-size: 13px;
        }
    }

    .chevron {
        width: 16px;
        height: 16px;

        img {
            width: 16px;
            height: 9px;
        }
    }

    .text {
        font-size: 12px;
        line-height: 17px;
    }
}