header {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    height: 163px;
    padding: 45px 0 50px 0;
  
    img {
      width: auto;
      height: 68px;
    }
  
    nav a {
      display: inline-block;
      font-size: 24px;
      font-weight: 500;
      text-decoration: none;
      text-align: right;
      color: black;
      width: 136px;
  
      // NavLink: page est active
      &.active {
        text-decoration: underline;
      }
    }
  
    @media screen and (min-width: 320px) and (max-width: 767.98px) {
      height: 94px;
      padding: 20px 0 27px 0;
  
      img {
        width: 145px;
        height: auto;
      }
  
      nav a {
        font-size: 12px;
        font-weight: 500;
        text-transform: uppercase;
        width: 95px;
      }
    }
  }
  